// this includes endpoints at data.nba.com
var getJson = require("./get-json");

var _require = require("./util/string"),
    interpolate = _require.interpolate;

var scoreboardURL = interpolate("http://data.nba.com/data/5s/json/cms/noseason/scoreboard/__date__/games.json");
var boxScoreURL = interpolate("http://data.nba.com/data/5s/json/cms/noseason/game/__date__/__gameId__/boxscore.json");
var playByPlayURL = interpolate("http://data.nba.com/data/5s/json/cms/noseason/game/__date__/__gameId__/pbp_all.json");
var scheduleURL = interpolate("http://data.nba.com/data/10s/prod/v1/__season__/schedule.json");
var teamScheduleURL = interpolate("http://data.nba.com/data/10s/prod/v1/__season__/teams/__teamId__/schedule.json");
var previewArticleURL = interpolate("http://data.nba.com/data/10s/prod/v1/__date__/__gameId___preview_article.json");
var recapArticleURL = interpolate("http://data.nba.com/data/10s/prod/v1/__date__/__gameId___recap_article.json");
var leadTrackerURL = interpolate("http://data.nba.com/data/10s/prod/v1/__date__/__gameId___lead_tracker___period__.json");
var playoffsBracketURL = interpolate("http://data.nba.com/data/10s/prod/v1/__season__/playoffsBracket.json");
var teamLeadersURL = interpolate("http://data.nba.com/data/10s/prod/v1/__season__/teams/__teamId__/leaders.json");
var teamStatsRankingsURL = interpolate("http://data.nba.com/data/10s/prod/v1/__season__/team_stats_rankings.json");
var coachesURL = interpolate("http://data.nba.com/data/10s/prod/v1/__season__/coaches.json"); // NOTE: the 'date' argument should be a string in format like "20181008" (which indicates Oct 8 2018)
// You *can* pass a Date object but beware of timezone weirdness!
// NOTE: the 'season' argument is the first year of the NBA season e.g. "2018" for the 2018-19 season

var scoreboard = function scoreboard(date) {
  return getJson(scoreboardURL({
    date: dateToYYYYMMDD(date)
  }));
};

scoreboard.defaults = {
  date: null
};

var boxScore = function boxScore(date, gameId) {
  return getJson(boxScoreURL({
    date: dateToYYYYMMDD(date),
    gameId: gameId
  }));
};

boxScore.defaults = {
  date: null,
  gameId: null
};

var playByPlay = function playByPlay(date, gameId) {
  return getJson(playByPlayURL({
    date: dateToYYYYMMDD(date),
    gameId: gameId
  }));
};

playByPlay.defaults = {
  date: null,
  gameId: null
};

var schedule = function schedule(season) {
  return getJson(scheduleURL({
    season: season
  }));
};

schedule.defaults = {
  season: null
};

var teamSchedule = function teamSchedule(season, teamId) {
  return getJson(teamScheduleURL({
    season: season,
    teamId: teamId
  }));
};

teamSchedule.defaults = {
  season: null,
  teamId: null
};

var previewArticle = function previewArticle(date, gameId) {
  return getJson(previewArticleURL({
    date: dateToYYYYMMDD(date),
    gameId: gameId
  }));
};

previewArticle.defaults = {
  date: null,
  gameId: null
};

var recapArticle = function recapArticle(date, gameId) {
  return getJson(recapArticleURL({
    date: dateToYYYYMMDD(date),
    gameId: gameId
  }));
};

recapArticle.defaults = {
  date: null,
  gameId: null
};

var leadTracker = function leadTracker(date, gameId, period) {
  return getJson(leadTrackerURL({
    date: dateToYYYYMMDD(date),
    gameId: gameId,
    period: period
  }));
};

leadTracker.defaults = {
  date: null,
  gameId: null,
  period: null
};

var playoffsBracket = function playoffsBracket(season) {
  return getJson(playoffsBracketURL({
    season: season
  }));
};

playoffsBracket.defaults = {
  season: null
};

var teamLeaders = function teamLeaders(season, teamId) {
  return getJson(teamLeadersURL({
    season: season,
    teamId: teamId
  }));
};

teamLeaders.defaults = {
  season: null,
  teamId: null
};

var teamStatsRankings = function teamStatsRankings(season) {
  return getJson(teamStatsRankingsURL({
    season: season
  }));
};

teamStatsRankings.defaults = {
  season: null
};

var coaches = function coaches(season) {
  return getJson(coachesURL({
    season: season
  }));
};

coaches.defaults = {
  season: null
};

function dateToYYYYMMDD(date) {
  if (date instanceof Date) {
    return [date.getFullYear(), String(date.getMonth() + 1).padStart(2, 0), String(date.getDate()).padStart(2, 0)].join("");
  } // TODO: better checking here?


  return date;
}

module.exports = {
  scoreboard: scoreboard,
  boxScore: boxScore,
  playByPlay: playByPlay,
  schedule: schedule,
  teamSchedule: teamSchedule,
  previewArticle: previewArticle,
  recapArticle: recapArticle,
  leadTracker: leadTracker,
  playoffsBracket: playoffsBracket,
  teamLeaders: teamLeaders,
  teamStatsRankings: teamStatsRankings,
  coaches: coaches
};