var camelCase = require("camel-case");

var parameters = [{
  name: "name",
  default: "offensive",
  values: ["offensive", "defensive"]
}, {
  name: "seasonType",
  default: "Reg",
  values: ["Reg", "Post"]
}, {
  name: "category",
  default: null,
  values: ["Transition", "PRBallHandler", "PRRollman", "Postup", "Spotup", "Handoff", "Cut", "OffScreen", "OffRebound", "Misc"]
}, {
  name: "season",
  default: 2017
}, {
  name: "limit",
  default: 50
}];
var synergyEndpoints = [{
  name: "player_play_type",
  url: "http://stats-prod.nba.com/wp-json/statscms/v1/synergy/player/"
}, {
  name: "team_play_type",
  url: "http://stats-prod.nba.com/wp-json/statscms/v1/synergy/team/"
}];
var defaults = {};
parameters.forEach(function (param) {
  defaults[param.name] = param.default;
});

function makeSynergyMethod(endpoint, transport) {
  function synergyMethod() {
    var query = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : {};
    var reqParams = Object.assign({}, defaults, query);
    return transport(endpoint.url, reqParams).then(function (response) {
      return response;
    });
  }

  synergyMethod.defaults = defaults;
  return synergyMethod;
}

function makeSynergyClient(transport) {
  var client = {};
  synergyEndpoints.forEach(function (endpoint) {
    client[camelCase(endpoint.name)] = makeSynergyMethod(endpoint, transport);
  });
  client.withTransport = makeSynergyClient;
  return client;
}

module.exports = makeSynergyClient(require("./get-json"));